import { ADD_LABEL, EDIT_LABEL, REMOVE_LABEL, SET_LABELS } from "../constants";
const reducer = (state, action) => {
    switch (action.type) {
        case SET_LABELS: {
            return Object.assign(Object.assign({}, state), { labels: action.payload });
        }
        case ADD_LABEL: {
            return Object.assign(Object.assign({}, state), { labels: [...state.labels, action.payload] });
        }
        case EDIT_LABEL: {
            return Object.assign(Object.assign({}, state), { labels: state.labels.map((label) => label.id === action.payload.id ? action.payload : label) });
        }
        case REMOVE_LABEL: {
            return Object.assign(Object.assign({}, state), { labels: state.labels.filter((label) => label.id !== action.payload.id) });
        }
        default: {
            return state;
        }
    }
};
export { reducer };
